/* fonts */
@font-face {
  font-family: 'Avignon Book';
  src: url('https://app.woodpecker.co/css/fonts/avignon-regular-webfont.eot');
  src: url('https://app.woodpecker.co/css/fonts/avignon-regular-webfont.eot?#iefix') format('embedded-opentype'),
  url('https://app.woodpecker.co/css/fonts/avignon-regular-webfont.woff') format('woff'),
  url('https://app.woodpecker.co/css/fonts/avignon-regular-webfont.ttf') format('truetype'),
  url('https://app.woodpecker.co/css/fonts/avignon-regular-webfont.svg#avignonbook') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Avignon Demi';
  src: url('https://app.woodpecker.co/css/fonts/avignon-demi-webfont.eot');
  src: url('https://app.woodpecker.co/css/fonts/avignon-demi-webfont.eot?#iefix') format('embedded-opentype'),
  url('https://app.woodpecker.co/css/fonts/avignon-demi-webfont.woff') format('woff'),
  url('https://app.woodpecker.co/css/fonts/avignon-demi-webfont.ttf') format('truetype'),
  url('https://app.woodpecker.co/css/fonts/avignon-demi-webfont.svg#avignondemi') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Avignon Italic';
  src: url('https://app.woodpecker.co/css/fonts/avignon-italic-webfont.eot');
  src: url('https://app.woodpecker.co/css/fonts/avignon-italic-webfont.eot?#iefix') format('embedded-opentype'),
  url('https://app.woodpecker.co/css/fonts/avignon-italic-webfont.woff') format('woff'),
  url('https://app.woodpecker.co/css/fonts/avignon-italic-webfont.ttf') format('truetype'),
  url('https://app.woodpecker.co/css/fonts/avignon-italic-webfont.svg#avignondemi') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Bradley';
  src: url('https://app.woodpecker.co/css/fonts/bradley.eot');
  src: url('https://app.woodpecker.co/css/fonts/bradley.eot?#iefix') format('embedded-opentype'),
       url('https://app.woodpecker.co/css/fonts/bradley.woff') format('woff'),
       url('https://app.woodpecker.co/css/fonts/bradley.ttf') format('truetype'),
       url('https://app.woodpecker.co/css/fonts/bradley.svg#avignondemi') format('svg');
  font-weight: normal;
  font-style: normal;
}

/* colors */
$green: #97c33a;
$green-hover: #a5d443;
$grey: #cdcbcb;
$white: #fff;
$black: #0b0204;
$blue: #1b88c7;
$blue-hover: #2098dd;
$light-grey: #bdbdbd;
$lighter-grey: #e7e6e6;

$fontBook: 'Avignon Book';
$fontDemi: 'Avignon Demi';
$fontItalic: 'Avignon Italic';

.woodpecker-options-wrap {
  margin-top: 20px;
  margin-right: 20px;
  background-color: $black;
  font-family: $fontBook;
}

.toplevel_page_wfw {
  .wp-menu-image {
    text-align: center !important;
    display: block;
    padding: 0;
    margin: 0;

    img {
      display: block;
      width: auto;
      max-height: 100%;
      margin: 0 auto !important;
      padding: 5px 0 0 0 !important;
      height: 25px;
    }
  }
}

#woodpecker-options {
  a {
    outline: none;

    &:focus {
      box-shadow: unset;
    }
  }

  img {
    max-width:100%;
    height: auto;
  }

  h1 {
    color: $white;
    font-weight: normal;
    font-family: $fontDemi;
    margin-top: 0;
    padding-top: 0;
    margin-bottom: 0;
    line-height: 1em;
    padding-left: 10px;
  }

  h5 {
    font-size: 16px;
    font-weight: normal;
  }

  .close {
    background-image: url("../images/button_delete_small.svg");
    width: 8px;
    height: 8px;
  }

  .fontDemi{
    font-family: $fontDemi;
  }

  .fonBook{
    font-family: $fontBook;
  }

  .uppercase {
    text-transform: uppercase;
  }

  .hand {
    cursor: pointer;
  }

  .textLink {
    text-decoration: underline;
    cursor: pointer;

    &:hover {
      text-decoration: none;
    }
  }

  .linkLabel {
    text-decoration: underline;
    color: #1b88c7;
    font-size: 14px;
    cursor: pointer;
    transition: .3s ease;

    &:hover {
      text-decoration: none;
      color: #2098dd;
    }
  }

  input {
    &::-webkit-input-placeholder {
      font-family: $fontItalic;
      color: #d7d7d7;
    }

    &:-ms-input-placeholder {
      font-family: $fontItalic;
      color: #d7d7d7;
    }

    &::placeholder {
      font-family: $fontItalic;
      color: #d7d7d7;
    }
  }

  input[type="text"], input[type="password"] {
    border-left: 0;
    border-right: 0;
    border-top: 0;
    background-color: transparent;
    border-bottom: 1px solid $grey;
    box-shadow: none;
    padding-left: 0;
    padding-right: 0;
    font-size: 16px;
    font-family: "Avignon Book";
    transition: all .3s ease;
    border-radius: 0;
    color: $black;
    line-height: normal;

    &:focus {
      border-bottom: 1px solid $black;
    }
  }

  .button {
    border-color: #d7d7d7;
    color: $black;

    &:focus {
      box-shadow: unset;
    }
  }

  .btn-woodpecker {
    background-color: $green;
    text-transform: uppercase;
    padding: 0 15px;
    line-height: 0;
    color: $white;
    border-radius: 0;
    border: 0;
    font-size: 16px;
    font-family: "Avignon Book";
    width: 107px;
    transition: all .3s ease;

    &:hover {
      background-color: $green-hover;
      border: 0;
    }
  }

  textarea {
    width: 100%;
    max-width: 825px;
    resize: none;
    border: 1px solid $grey;
    box-shadow: none;
    font-size: 16px;
    padding: 23px;
    min-height: 86px;
    transition: all .3s ease;

    &:focus {
      border: 1px solid $black;
    }
  }

  .col-container {
    background-color: $white;
    border: 1px solid #ccc;
    padding: 50px 20px 20px 20px;

    &__margin {
      margin: auto;
      max-width: 1366px;
    }
  }
}
