#woodpecker-options {
  .save-box.active {
    opacity: 1;
    height: 29px;
    background-color: #fff;
    overflow: hidden;
  }

  .save-box.fixed {
    position: fixed;
    z-index: 999;
  }

  .save-box {
    width: 100%;
    max-width: 843px;
    padding: 43px 0;
    border-top: 1px solid #e7e6e6;
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition: all .3s ease;
    position: absolute;
    bottom: 0;

    &__close {
      background-image: url('../images/button_close.svg');
      float: right;
      width: 16px;
      height: 16px;
      background-repeat: no-repeat;
      background-position: center;
      margin-top: 7px;
      cursor: pointer;
    }

    .submit {
      display: inline-block;
      padding: 0;
      margin: 0;
    }
  }
}
