#woodpecker-options {
  .prospect-content {
    display: block;
    width: 100%;

    .tags-panel {
      min-height: 53px;
      margin-top: 12px;
      margin-bottom: 5px;
    }
     .filter-panel {
      span:nth-of-type(3) {
        margin-left: 19px;
      }
     }

    h5 {
      font-family: $fontDemi;
      text-transform: uppercase;
      font-size: 22px;
      margin-bottom: 64px;
      margin-top: 44px;
    }

    &__table {
      font-size: 16px;

      &--row.title {
        font-size: 12px;
        border-top: 1px solid #e7e6e6;
        text-transform: uppercase;
        padding: 9px 15px;
      }

      &--row {
        display: grid;
        //135px 360px 360px 360px 134px
        grid-template-columns: 12% 25% 25% 25% 13%;
        border-bottom: 1px solid #e7e6e6;
        padding: 25px 15px;

        .row-table {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            padding: 0 15px 0 0;
            height: 19px;
        }
      }
    }
  }
}
