#woodpecker-options {
  .popupTooltip__content--item {
    .tooltip {
      bottom: 35px;
    }
  }

  #blackTooltipShortcode {
    max-width: 77px;
    left: -30px;
    bottom: 10px;
    word-break: normal;
    white-space: nowrap;
  }

  #blackTooltipShortcodeMain {
    max-width: 77px;
    left: -38px;
    bottom: 28px;
    word-break: normal;
    white-space: nowrap;
  }

  .tooltip {
    bottom: 5px;
    font-size: 11px;
    padding: 5px 10px 0 10px;
    max-width: 100%;
    width: max-content;
    position: absolute;
    display:block;
    white-space: normal;
    background-color: #000;
    color: #FFF;
    font-family: $fontBook;
    word-break: break-word;
    margin: 0;
    z-index: 99;
    line-height: 15px;
    text-align: justify;

    &::after {
      content: "";
      background-color: #000;
      transform: rotate(45deg);
      position: relative;
      height: 10px;
      width: 10px;
      bottom: -4px;
      margin: 0 auto;
      display: block;
    }
  }
}
