#woodpecker-options {

  .settings-container {
    display: flex;
    width: 100%;
    min-height: 750px;
    margin-top: 27px;

    #wfw-privacy_policy {
      margin-bottom: 0;
    }

    .disable {
      input {
        color: #d0d0d0;
      }

      .settings-container__content--span {
          color: #dcdedf;
      }
    }

    input.error {
      font-size: 16px;
    }

    .error {
      margin-top: 10px;
      color: #ec1940;
      font-size: 14px;
      display: block;
      padding-left: 2px;
    }

    .info {
      margin-top: 10px;
      color: #1b88c7;
      font-size: 14px;
      display: block;
      padding-left: 7px;

      &::before {
        content: "";
        background-image: url('../images/icon_info.svg');
        height: 18px;
        width: 18px;
        background-repeat: no-repeat;
        background-position: center;
        left: -7px;
        position: relative;
        display: inline-block;
        top: 4px;
      }
    }

    &__menu {
      width: 210px;
      height: 280px;
      position: sticky;
      top: 30px;
      margin-right: 100px;

      ul {
        font-size: 16px;
        font-family: 'Avignon Book';
        padding-left: 38px;

        li.active{
          font-family: 'Avignon Demi';
          position: relative;

          &:before{
            content: "";
            width: 17px;
            height: 3px;
            background-color: $green;
            position: absolute;
            transition: width .3s ease;
            left: -38px;
            display: block;
            top: 50%;
          }
        }

        li {
          margin-bottom: 44px;
          position: relative;
          cursor: pointer;

          &:before{
            content: "";
            width: 0;
            height: 3px;
            background-color: $green;
            position: absolute;
            transition: width .3s ease;
            left: -38px;
            display: block;
            top: 50%;
          }

          &:hover {
            &:before{
              content: "";
              width: 17px;
            }
          }
        }
      }
    }

    &__footer {
      display: block;
      height: 116px;
      width: 100%;
    }

    &__content {
      width: 100%;
      display: block;
      position: relative;
      padding-top: 16px;

      &--box {
        display: flex;
        width: 100%;
        max-width: 545px;
        margin-bottom: 32px;
        position: relative;
      }

      &--selector {
        margin-top: 60px;

        &__box {
          margin-bottom: 38px;
          display: flex;
          width: 100%;
          max-width: 545px;
        }

        &__span {
          font-family: $fontDemi;
          font-size: 16px;
          text-transform: uppercase;
          margin-right: 14px;
          padding-top: 3px;
          white-space: nowrap;
        }
      }

      &--category {
        font-family: $fontDemi;
        font-size: 16px;
        text-transform: uppercase;
        display: block;
        margin-bottom: 40px;
        cursor: text;
      }

      &--pickers {
        margin-top: 70px;

        &__box {
          margin-bottom: 32px;
          display: flex;
          width: 100%;
          max-width: 545px;
        }

        &__span {
          font-size: 16px;
          text-transform: uppercase;
          margin-right: 14px;
          padding-top: 3px;
          white-space: nowrap;
        }
      }

      &--checkbox {
        position: absolute;
        right: -73px;
      }

      &--span {
        font-size: 16px;
        text-transform: uppercase;
        margin-right: 14px;
        padding-top: 6px;
        white-space: nowrap;
      }

      &--label {
        font-size: 16px;
        text-transform: uppercase;
        font-family: 'Avignon Demi';
        margin-bottom: 24px;
        display: block;
        width: 100%;
        cursor: default;
      }

      &--input {
        width: 100%;
        max-width: 843px;
        display: block;
        border-top: 0;
        border-left: 0;
        border-right: 0;
        outline: none;
      }

      &--input:focus {
        border-top: 0;
        border-left: 0;
        border-right: 0;
        outline: none;
      }

      &--scroll {
        border: 1px solid #e7e6e6;
        height: 164px;
        max-width: 843px;
        height: 164px;
        margin-bottom: 56px;
        padding: 23px 10px 23px 23px;
        transition: .3s ease;

        &.active {
          border: 1px solid $black;
        }
      }

      &--textarea {
        max-width: 843px;
        width: 100%;
        max-height: 162px;
        resize: none;
        border: 0;
        border-radius: 0;
        line-height: normal;
        padding: 0 13px 0 0;

        &:focus {
          outline: none;
          border: 0;
        }
      }

      .api.error {
        border-bottom: 1px solid  #ec1940;
      }
    }

    .wfw-checkbox {
      position: relative;
      margin-left: 23px;
    }

    .wfw-checkbox, .settings-container__content--checkbox {
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      margin-top: 4px;

      .custom-checkbox {
        position: absolute;
        top: 0;
        bottom: 0;
        margin-top: auto;
        margin-bottom: auto;
        left: -23px;
        display: block;
        height: 15px;
        width: 15px;
        border: 1px solid #e1e1e1;
        border-radius: 2px;
        transition: all .3s ease;
        z-index: 98;

        &:after {
          content: "";
          position: absolute;
          opacity: 0;
          background-image: url('../images/tick.svg');
          height: 8px;
          width: 8px;
          top: 1px;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
          background-size: contain;
          transition: all .3s ease;
        }
      }

      .show-input-checkbox:checked ~ .custom-checkbox:after, .settings-container__content--checkbox__input:checked ~ .custom-checkbox:after {
        opacity: 1;
      }

      .show-input-checkbox, .settings-container__content--checkbox__input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 17px;
        width: 17px;
        top: 0;
        bottom: 0;
        margin: auto;
        left: -23px;
        z-index: 99;
      }
    }
  }
}
